import React, { useEffect, useState, useRef } from 'react';
import { nanoid } from 'nanoid';
import { gsap } from 'gsap';
import Ripple from '../MagicUI/Ripple';
import FlipText from '../MagicUI/FlipText';
import { useMediaQuery } from 'react-responsive';

interface ListItemProps {
  title: string;
  items: string[];
}

const ListItem: React.FC<ListItemProps> = ({ title, items }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const isMobile = useMediaQuery({ maxWidth: 767 }); // Adjust breakpoint as needed
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const menuRef = useRef<HTMLDivElement>(null);

  const handleMouseEnter = () => {
    setIsHovered(true);
    if (menuRef.current) {
      menuRef.current.style.transitionDelay = '0ms';
    }
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    setIsClicked(false);
    if (menuRef.current) {
      menuRef.current.style.transitionDelay = '300ms';
    }
  };

  const handleMouseClick = () => {
    setIsClicked(!isClicked);
  };

  return (
    <div
      className="group relative mb-4 overflow-hidden rounded-l-full py-5 text-[#290D05]"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onFocus={handleMouseEnter}
      onBlur={handleMouseLeave}
      onClick={handleMouseClick}
      aria-expanded={!isMobile ? isHovered : isMenuOpen}
      aria-haspopup="true"
    >
      {/* menu item background */}
      <div
        className={`absolute left-0 top-0 h-full w-0 rounded-full bg-primary transition-all duration-300 ease-out group-hover:w-[calc(100%-16px)]`}
      >
        <div
          className={`bg-background absolute right-0 flex h-full w-full max-w-[40rem] items-center justify-center overflow-hidden rounded-full object-contain ${isClicked ? 'opacity-100' : 'opacity-0'}`}
        >
          <Ripple />
        </div>
      </div>
      <div className={`${isMobile ? 'hidden' : ''}`}>
        <div
          className={`relative z-10 p-6 pr-40 transition-colors duration-300 ease-in-out group-hover:text-white`}
        >
          <h3 className="text-2xl font-medium">{title}</h3>
        </div>
        <div
          ref={menuRef}
          className={`absolute bottom-2 right-16 top-5 grid w-fit grid-cols-2 items-start justify-center gap-x-10 transition-opacity duration-300 ease-in-out ${isClicked ? 'opacity-100' : 'opacity-0'} `}
          onClick={handleMouseClick}
        >
          {items.map((item, index) => (
            <div key={nanoid()}>
              <FlipText
                className="text-sm tracking-[-0.1em] text-white"
                word={item}
              />
            </div>
          ))}
        </div>
      </div>

      {isMobile ? (
        <>
          <button
            className={`relative left-4 flex h-12 items-center md:h-24 ${isClicked ? 'text-white' : 'text-black'}`}
          >
            <div className="flex flex-grow items-center">
              <h3 className="truncate text-sm font-medium sm:text-sm md:text-xl">
                {title}
              </h3>
              {window.innerWidth > 480 && (
                <svg
                  className={`ml-2 h-4 w-4 fill-current transition-transform ${
                    isClicked ? 'rotate-180 text-white' : 'text-black'
                  }`}
                  viewBox="0 0 24 24"
                >
                  <path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z" />
                </svg>
              )}
            </div>
          </button>

          {isClicked && (
            <div
              className={`relative left-10 top-1 flex w-fit flex-col transition-all duration-300 ${isClicked ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}`}
            >
              {items.map((item, index) => (
                <div key={nanoid()}>
                  <FlipText
                    className="text-sm font-medium sm:text-sm md:text-xl tracking-[-0.1em] text-white"
                    word={item}
                  />
                </div>
              ))}
            </div>
          )}
        </>
      ) : (
        <div
          ref={menuRef}
          className={`absolute bottom-2 right-16 top-5 grid w-fit grid-cols-2 items-start justify-center gap-x-10 transition-all duration-300 ${isClicked ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}`}
          onClick={handleMouseClick}
        ></div>
      )}
    </div>
  );
};

export default ListItem;
